.section-about {
    background-color: rgba($color-grey-light-1, 0.6);
    padding: 25rem 0;
    margin-top: -20vh;

    @include respond(tab-port) {
        padding: 20rem 0;
    }
}
.section-features {
    padding: 20rem 0;
    background-image: linear-gradient(
            to right bottom,
            rgba($color-primary-light, 0.8),
            rgba($color-primary-dark, 0.8)
        ),
        url('../img/career.jpg');
    background-size: cover;
    transform: skewY(-7deg);
    margin-top: -10rem;

    &__heading {
        text-align: center;
        margin-bottom: 5rem;
        font-size: 5rem;
        color: $color-white;
        letter-spacing: 0.5rem;
        cursor: pointer;

        &:hover {
            transform: skewy(2deg) skewX(15deg) scale(1.1);
            text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
        }
    }

    & > * {
        transform: skewY(7deg);
    }

    @include respond(tab-port) {
        padding: 10rem 0;
    }
}

.section-projects {
    background-color: $color-grey-light-1;
    padding: 25rem 0;
    margin-top: -10rem;

    @include respond(tab-port) {
        padding: 20rem 0 10rem 0;
    }
}

.section-stories {
    position: relative;
    padding: 15rem 0;

    @include respond(tab-port) {
        padding: 10rem 0;
    }
}

.section-book {
    padding: 15rem 0;
    background-image: linear-gradient(
        to right bottom,
        $color-primary-light,
        $color-primary-dark
    );
}

.book {
    background-image: linear-gradient(
            105deg,
            rgba($color-white, 0.9) 0%,
            rgba($color-white, 0.9) 50%,
            transparent 50%
        ),
        url('../img/nat-10.jpg');
    background-size: 100%;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.2);

    @include respond(tab-land) {
        background-image: linear-gradient(
                105deg,
                rgba($color-white, 0.9) 0%,
                rgba($color-white, 0.9) 65%,
                transparent 65%
            ),
            url('../img/nat-10.jpg');
        background-size: cover;
    }

    @include respond(tab-port) {
        background-image: linear-gradient(
                to right,
                rgba($color-white, 0.9) 0%,
                rgba($color-white, 0.9) 100%
            ),
            url('../img/nat-10.jpg');
    }

    &__form {
        width: 50%;
        padding: 6rem;
        @include respond(tab-land) {
            width: 65%;
        }

        @include respond(tab-port) {
            width: 100%;
        }
    }
}
